export default "import { JSX } from 'react';\n\nimport { Combobox } from '@skatteetaten/ds-forms';\nimport { Paragraph } from '@skatteetaten/ds-typography';\n\nconst options = [\n  {\n    label: 'Agdenes',\n    value: 'agdenes',\n  },\n  {\n    label: 'Balestrand',\n    value: 'balestrand',\n  },\n  {\n    label: 'Finnøy',\n    value: 'finnoy',\n  },\n  {\n    label: 'Forsand',\n    value: 'forsand',\n  },\n  {\n    label: 'Kvalsund',\n    value: 'kvalsund',\n  },\n  {\n    label: 'Mandal',\n    value: 'mandal',\n  },\n  {\n    label: 'Midsund',\n    value: 'midsund',\n  },\n  {\n    label: 'Rennesøy',\n    value: 'rennesoy',\n  },\n  {\n    label: 'Songdalen',\n    value: 'songdalen',\n  },\n  {\n    label: 'Tysfjord',\n    value: 'tysfjord',\n  },\n];\n\nconst ComboboxMaxSelectedExample = (): JSX.Element => {\n  return (\n    <>\n      <Paragraph hasSpacing>\n        {\n          'Eksempel på bruk av combobox med maks antall valg satt til 3. Når brukeren har valgt 3 kommuner, vil de ikke kunne velge flere.'\n        }\n      </Paragraph>\n      <Combobox\n        name={'favoriteKommuner'}\n        label={'Velg dine 3 favorittkommuner'}\n        options={options}\n        maxSelected={3}\n        multiple\n        hasSpacing\n      />\n    </>\n  );\n};\n\nexport default ComboboxMaxSelectedExample;\n"